  font-size: .8rem;


}

.App {
  position:absolute;
  width:100%;
  min-height:100%;
  display:flex;
  flex-direction:column;
  overflow:hidden;
  overflow-y:auto;
}


.drp-dwn-btn-frame{
  display:flex;
  justify-content:center;
  align-items:center;
}
