}


.subscribe input ,.report-password textarea,.report-password select{
    -webkit-text-fill-color:#000;
  color:#000;
}

.cs-imgs{
  width:100%;
  max-height:225px;
}

.x-link{
  text-shadow:0 0 5px red;
   -webkit-text-fill-color:red;
  color:red;
  font-weight: bolder;
  -webkit-text-decoration-color: gold;
          text-decoration-color: gold;
